.jumbotron {
  min-height: 300px;
  padding-top: 75px;
  background: -webkit-gradient(linear, left top, left bottom, from(black), color-stop(rgb(89, 43, 63)), to(rgba(0, 0, 0, 0.454)));
  background: -webkit-linear-gradient(black, rgb(89, 43, 63), rgba(0, 0, 0, 0.454));
  background: -o-linear-gradient(black, rgb(89, 43, 63), rgba(0, 0, 0, 0.454));
  background: linear-gradient(black, rgb(89, 43, 63), rgba(0, 0, 0, 0.454));
}

h2{
  color: white;
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 40px;
}


.jumbotron h1, .jumbotron .h1 {
    font-size: 68px;
    color: white;
    font-family: 'Space Mono', monospace;
    font-size: 110px;
}


