body{
  background: -webkit-gradient(linear, left top, left bottom, from(black), color-stop(rgb(81, 43, 89)), color-stop(rgba(85, 11, 233, 0.454)), to(rgb(23, 111, 113)));
  background: -webkit-linear-gradient(black, rgb(81, 43, 89), rgba(85, 11, 233, 0.454), rgb(23, 111, 113));
  background: -o-linear-gradient(black, rgb(81, 43, 89), rgba(85, 11, 233, 0.454), rgb(23, 111, 113));
  background: linear-gradient(black, rgb(81, 43, 89), rgba(85, 11, 233, 0.454), rgb(23, 111, 113));
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 20px;
}


.display-card {
  background: rgb(255, 255, 255);
  border-style: solid;
  height: 415px;
  margin: 1rem;
  position: relative;
  width: 80%;
  -webkit-box-shadow: 10px 3px 26px 5px rgba(0, 0, 0, 0.5);
          box-shadow: 10px 3px 26px 5px rgba(0, 0, 0, 0.5);
  text-align: left;
  border-radius: 4px;
}
.display-image {
  height: 60%;
  overflow: hidden;
  text-align: center;
  background:rgb(17, 130, 211);
}

.display-image > img {
  width: 100%
}

.display-content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 18.5px;
  margin-bottom: 20px;
  /* overflow: scroll; */
}

.btn:hover{
  color: red;
}


.display-content + button{
  position: absolute;
  bottom: 0;
  padding:5px 10px;
  opacity: 0;
  -webkit-animation: info 1s ease-in-out 2s forwards;
          animation: info 1s ease-in-out 2s forwards;
}


@-webkit-keyframes info{
  0%{
    opacity: 0;
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
  }

  50%{
    -webkit-transform: translate(160px, -130px);
            transform: translate(160px, -130px);
  }

  100%{
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
    
  }
}


@keyframes info{
  0%{
    opacity: 0;
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
  }

  50%{
    -webkit-transform: translate(160px, -130px);
            transform: translate(160px, -130px);
  }

  100%{
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
    
  }
}


