.col-sm-12{
    -webkit-animation: bottom_top 2s ease-in-out;
            animation: bottom_top 2s ease-in-out;
}


@-webkit-keyframes bottom_top{
    0%{
        -webkit-transform: translate(-500px, 1200px) rotate(45deg);
                transform: translate(-500px, 1200px) rotate(45deg);
    }

    100%{
        -webkit-transform: translate(0px, 0px) rotate(0deg);
                transform: translate(0px, 0px) rotate(0deg);
    }
}


@keyframes bottom_top{
    0%{
        -webkit-transform: translate(-500px, 1200px) rotate(45deg);
                transform: translate(-500px, 1200px) rotate(45deg);
    }

    100%{
        -webkit-transform: translate(0px, 0px) rotate(0deg);
                transform: translate(0px, 0px) rotate(0deg);
    }
}

.display-card{
    position: relative;
}


